    &:focus {
      outline: none;
    }
  }
}

// Deprecated
.ColVis_catcher {
  position: absolute;
  z-index: 999;
}

// Deprecated
.ColVis_collection {
  background-color: @dropdown-bg;
  border: 1px solid @dropdown-border;
  border-radius: @border-radius-base;
  .box-shadow(0 6px 12px fade(@color-pf-black, 17.5%));
  background-clip: padding-box;
  list-style: none;
  margin: -1px 0 0 0;
  padding: 5px 10px;
  width: 150px;
  z-index: @zindex-dropdown;
  label {
    font-weight: normal;
    margin-bottom: 5px;
    margin-top: 5px;
